body {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: "Montserrat-Regular", sans-serif;
  background-color: #fff;
  font-size: 1rem;
  font-weight: 400;
  line-height: 1.5;
  color: #212529;
}

.wrap_header {
  display: flex;
  flex-wrap: wrap;
  width: 100%;
  height: 80px;
  background-color: white;
  justify-content: center;
  align-items: center;
  position: relative;
  transition: all 0.3s;
}

.header1 {
  height: 80px;
  transition: all 0.3s;
  width: 100%;
  top: 0;
  left: 0;
  position: fixed;
  z-index: 1100;
  box-shadow: 0 1px 5px 0px rgba(0, 0, 0, 0.2);
}
.main_menu {
  list-style-type: none;
  margin: 0px;
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  justify-content: center;
}

.logo {
  display: block;
  position: absolute;
  left: 52px;
  top: 50%;
  -webkit-transform: translateY(-50%);
          transform: translateY(-50%);
}

.logo img {
  max-height: 27px;
}
.header-icons {
  display: flex;
  align-items: center;
  position: absolute;
  right: 52px;
  top: 50%;
  -webkit-transform: translateY(-50%);
          transform: translateY(-50%);
}

.page_container {
  margin-top: 80px;
  min-height: 100vh;
}
.carrousel_wrapper {
  overflow: hidden;
}

.carrousel_image {
  min-height: 88vh;
}

.main_loader {
  text-align: center;
  margin-top: 100px;
}

.page_wrapper {
  padding-top: 85px;
}
.container {
  max-width: 1100px;
  margin: 0 auto;
}
a {
  text-decoration: none;
  color: inherit;
}

.dialog_alert {
  padding: 51px;
  font-size: 30px;
}

.load_more_container {
  clear: both;
  margin-bottom: 50px;
  border-top: 5px solid #f5f5f5;
  padding-top: 20px;
}

.load_more_container span {
  cursor: pointer;
  background: #2196f3;
  color: #ffffff;
  font-size: 27px;
  padding: 10px 60px;
  display: table;
  margin: 0 auto;
}

.no_result {
  font-size: 40px;
  color: #999592;
  text-align: center;
  padding-top: 40px;
}

.not_found_container {
  font-size: 70px;
  text-align: center;
  padding-top: 70px;
  color: #dddddd;
}

.clear:after {
  content: "";
  display: block;
  clear: both;
}

input {
  font-size: 15px;
  box-sizing: border-box;
  padding: 10px 10px;
  width: 100%;
  margin: 10px 0px 0px 0px;
}

.error_label {
  color: #f44336;
  display: block;
  font-size: 15px;
}

textarea {
  width: 100%;
  height: 51px;
  font-size: 15px;
  padding: 10px;
  box-sizing: border-box;
  margin-top: 5px;
  border: 1px solid #d1d1d1;
}

.form_devider {
  border-bottom: 3px solid #999592;
  margin: 40px 0px;
}

.label_inputs {
  display: inline-block;
  background: #999592;
  color: #fff;
  padding: 5px 5px;
}

select {
  font-size: 16px;
  height: 38px;
  border: 1px solid #e9e9e9;
  border-radius: 3px;
  width: 100%;
  margin: 10px 0px 0px 0px;
}
.formBlock {
  margin-bottom: 10px;
}

.form_block_two {
  display: flex;
}
.form_block_two .block {
  flex-grow: 1;
  box-sizing: border-box;
}

.form_block_two .block:nth-child(1) {
  padding-right: 10px;
}
.form_block_two .block:nth-child(2) {
  padding-left: 10px;
}

.form_success {
  background: #4caf50;
  color: #ffffffff;
  padding: 10px;
  display: block;
}

button {
  padding: 5px;
  display: table;
  border: solid 1px #333333;
  background: #c43235;
  color: #fff;
  margin-top: 10px;
}

.link_default {
  padding: 5px 45px;
  display: table;
  border: solid 1px #333333;
  background: #222222;
  color: #fff;
  margin-top: 30px;
  border-radius: 23px;
  cursor: pointer;
}

.payment {
  margin-top: 20px;
  margin-left: 100px;
}

.card_link,
.bag_link,
.add_to_cart_link {
  width: 100%;
  border: 1px solid #dcdcdc;
  padding: 5px 10px;
  display: block;
  box-sizing: border-box;
  text-align: center;
  font-size: 12px;
  text-transform: uppercase;
  color: #c3c0b1;
  background: #f8f8f8;
  transition: all 200ms ease-in;
  cursor: pointer;
}
.card_link:hover,
.bag_link:hover,
.add_to_cart_link:hover {
  background: #aaaaaa;
  color: #ffff;
}
.add_to_cart_link {
  color: #6b6b6b;
  background: #d9d9d9;
}
.add_to_cart_link svg {
  margin-right: 10px;
}

.action_button {
  text-transform: uppercase;
  padding: 10px;
  display: table;
  background: #e1ddc3;
  margin-top: 10px;
}

.page_top {
  border-bottom: 1px solid #999592;
  padding: 20px 0px;
  text-transform: uppercase;
}

header {
  position: fixed;
  z-index: 999;
  width: 100%;
  border-bottom: 1px solid white;
}

header .logo {
  font-family: "Monoton", cursive;
  color: #ffffff;
  font-size: 40px;
  padding: 11px 0px;
}

header a {
  margin-left: 20px;
}

header .icon-login .icon-cart {
  height: 100%;
}
header .icon-login img {
  vertical-align: middle;
  border-style: none;
}
header .icon-cart img {
  vertical-align: middle;
  border-style: none;
  position: absolute;
  right: 0px;
}

header .cart_link {
  display: inline-block;
  position: relative;
}

header .cart_link span {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 16px;
  height: 16px;
  border-radius: 50%;
  background-color: #111111;
  color: white;
  font-size: 12px;
  position: absolute;
  top: 0;
  left: -10px;
}
header .log_out_link {
  cursor: pointer;
  display: inline-block;
  margin-left: 20px;
}

footer .wrapper {
  padding: 45px;
  background-color: #f0f0f0;
}

footer .left {
  width: 33%;
}
footer .middle {
  width: 39%;
}
footer .right {
  width: 28%;
}
footer .flex-w {
  display: flex;
  color: #212529;
  opacity: 0.9;
  font-size: 12px;
  padding-bottom: 90px;
}

footer .newsletter {
  font-size: 15px;
  color: #222222;
  line-height: 1.5;
  text-transform: uppercase;
  padding-bottom: 15px;
}

.flex-w {
  display: flex;
  flex-wrap: wrap;
  padding-bottom: 90px;
}

footer .business_nfo {
  display: flex;
  flex-wrap: wrap;
}

footer .business_nfo .tag {
  flex-grow: 1;
  position: relative;
  width: 50%;
  margin-bottom: 30px;
}
footer .business_nfo .icon {
  position: absolute;
  font-size: 12px;
  top: 10px;
}
footer .business_nfo .nfo {
  padding-left: 40px;
}

.carrousel_wrapper .featured_action {
  position: absolute;
  top: 25%;
  left: 30%;
  text-align: center;
}

.carrousel_wrapper .featured_action a {
  position: absolute;
  top: 90%;
  left: 40%;
  padding: 5px 15px;
  background-color: #ffff !important;
  font-family: Montserrat-Regular;
  font-size: 15px;
  font-weight: 400;
  line-height: 1.7;
  color: rgb(17, 17, 17);
  text-transform: uppercase;
  border-radius: 23px;
  border: 1px solid #fff !important;
}

div.tag.title {
  font-family: "Montserrat-Regular";
  font-size: 18px;
  color: white;
  line-height: 1.5;
  letter-spacing: 2px;
}
div.tag.low_title {
  /* font-family: Poppins-Bold; */
  font-size: 60px;
  color: white;
  line-height: 1.2;
  text-transform: uppercase;
}

.home_promotion .home_promotion_img {
  height: 350px;
  background-size: cover !important;
  background-position: top center !important;
  padding: 15px;
  box-sizing: border-box;
  margin: 40px auto;
  position: relative;
}

.home_promotion .tag {
  color: #ffffff;
  padding: 0px 20px;
  text-transform: uppercase;
  display: table;
}

.home_promotion .tag.title {
  position: absolute;
  top: 15%;
  left: 43%;
  background: #82828287;
  font-size: 50px;
  font-weight: 700;
  margin: 10px auto;
}

.home_promotion .tag.low_title {
  position: absolute;
  top: 45%;
  left: 25%;
  font-size: 30px;
  font-weight: 300;
  margin-top: 10px;
  margin: 0 auto;
}

.home_promotion .my_link a {
  position: absolute;
  top: 55%;
  left: 45%;
}

.card_block {
  margin: 50px 0px 0px 0px;
}

.card_block .title {
  font-family: Montserrat-Bold;
  font-size: 30px;
  color: #222;
  line-height: 1.2;
  text-align: center;
  text-transform: uppercase;
  margin-bottom: 40px;
}

.card_block_shop .card_item_wrapper {
  float: left;
  width: 33.3%;
  margin-bottom: 20px;
}

.block2-btn-addcart {
  position: absolute;
  left: 50%;
  -webkit-transform: translateX(-50%);
  transform: translateX(-50%);
  bottom: -45px;
}

.cart {
  border-radius: 23px;
  background-color: rgb(17, 17, 17);
  width: 100%;
  height: 40px;
  font-family: Montserrat-Regular;
  font-size: 15px;
  color: white;
  text-transform: uppercase;
  display: flex;
  justify-content: center;
  align-items: center;
  transition: all 0.4s ease 0s;
  outline: none !important;
  border: none;
  background: transparent;
}

.card_item_wrapper {
  flex-grow: 1;
  width: 25%;
  padding: 0 15px;
  box-sizing: border-box;
  position: relative;
  overflow: hidden;
}

.overlay {
  position: absolute;
  top: 0;
  left: 15px;
  width: 90%;
  height: 350px;
  margin: 0 auto;
  background: rgba(0, 0, 0, 0);
  transition: background 0.5s ease;
}

.overlay:hover {
  display: block;
  background: rgba(0, 0, 0, 0.3);
}

.card_button {
  position: absolute;
  left: 60px;
  bottom: 80px;
  opacity: 0;
  transition: all 0.4s ease 0.1s;
  padding: 6px 25px;
  background-color: black;
  color: white;
  border: none;
  border-radius: 23px;
}

.card_button:hover {
  background-color: #ff6900;
  border: none;
}

.card_item_wrapper:hover .card_button {
  opacity: 1;
  left: 60px;
  bottom: 100px;
}

.card_item_wrapper {
  position: relative;
}

.card_item_wrapper .image {
  width: 100%;
  height: 350px;
  background-position: center !important;
  background-size: cover !important;
  border: 1px solid #f0f0f0;
}

.card_item_wrapper .tags {
  position: relative;
}

.card_item_wrapper .tags .brand,
.card_item_wrapper .tags .name,
.card_item_wrapper .tags .price {
  text-align: center;
  margin: 5px;
}

.card_item_wrapper .tags .brand {
  font-size: 17px;
  font-weight: 700;
  color: #2196f3;
}

.card_item_wrapper .tags .name {
  font-weight: 400;
}

.card_item_wrapper .tags .price {
  font-weight: 300;
  font-size: 21px;
}

.card_item_wrapper .actions {
  display: flex;
  padding: 15px 0 10px 0;
}

.card_item_wrapper .actions .button_wrapp:nth-child(1) {
  flex-grow: 1;
  padding-right: 10px;
}

.card_item_wrapper.grid_bars {
  float: none;
  width: 100%;
  display: flex;
  background: red;
}

.card_item_wrapper.grid_bars .image {
  width: 420px;
  height: 211px;
  border: none;
}

.card_item_wrapper.grid_bars .action_container {
  flex-grow: 1;
  border: 1px solid #e6e6e6;
}

.card_item_wrapper.grid_bars .description {
  clear: both;
  padding: 0px 25px;
  border-top: 1px solid #eeeeee;
}

.card_item_wrapper.grid_bars .tags {
  border-bottom: 0px;
  padding: 5px 20px;
}

.card_item_wrapper.grid_bars .tags .brand {
  font-size: 21px;
}

.card_item_wrapper.grid_bars .tags .brand,
.card_item_wrapper.grid_bars .tags .name {
  float: left;
}

.card_item_wrapper.grid_bars .tags .price {
  float: right;
}

.card_item_wrapper.grid_bars .actions {
  clear: both;
  float: right;
  padding: 10px 20px 0px 15px;
}

.shop_wrapper {
  display: flex;
}

.shop_wrapper .left {
  flex-grow: 1;
}

.shop_wrapper .right {
  width: 800px;
}

.collapse_title span {
  text-transform: uppercase;
  font-weight: 700;
}

.grid_btn {
  float: right;
  padding: 10px 10px;
  font-size: 20px;
  color: #c6c6c6;
}
.grid_btn.active {
  color: #727272;
}

.shop_options {
  border-bottom: 1px solid #e8e8e8;
  margin: 0px 20px 20px 20px;
}

.product_detail_wrapper {
  display: flex;
  margin-bottom: 140px;
}

.product_detail_wrapper .right {
  flex-grow: 1;
  min-height: 800px;
}

.product_detail_wrapper p {
  font-size: 17px;
  font-family: Arial, Helvetica;
  font-weight: lighter;
}

.product_detail_wrapper .product_tags {
  padding-bottom: 10px;
  border-bottom: 1px solid #d3d3d3;
}

.product_detail_wrapper .product_tags .tag {
  display: flex;
  margin-bottom: 20px;
}

.product_detail_wrapper .product_tags .tag svg {
  font-size: 43px;
  color: #e4e4e4;
  width: 50px;
}

.product_detail_wrapper .product_tags .tag .tag_text {
  font-size: 14px;
  text-transform: uppercase;
  color: #999592;
  padding-left: 20px;
}

.product_detail_wrapper .product_actions .price {
  font-size: 40px;
  font-weight: 300;
}
.product_detail_wrapper .product_actions .cart {
  width: 149px;
}
.user_cart_sum {
  border: 1px solid #e6e6e6;
  border-radius: 4px;
  max-width: 442px;
  margin-left: auto;
  margin-top: 30px;
  padding-right: 40px;
  padding-bottom: 38px;
  padding-top: 30px;
}

.user_cart_sum h4 {
  margin: 20px 50px;
  text-transform: uppercase;
}
.user_cart_sum span {
  text-align: center;
  margin-left: 50px;
}

.cart_no_items svg,
.cart_success svg {
  font-size: 130px;
  color: #717171;
}
.cart_no_items,
.cart_success {
  text-align: center;
  font-size: 28px;
  color: #999592;
}

.product_detail_wrapper .product_actions {
  padding: 20px 0px 30px 0px;
  /* border-bottom: 1px solid #d3d3d3; */
}

.product_detail_wrapper .product_specifications .item {
  font-size: 15px;
  text-transform: uppercase;
  color: #838383;
}

.product_detail_wrapper .product_image_container {
  margin-top: 30px;
  padding-right: 70px;
}

.product_detail_wrapper .main_pic {
  box-sizing: border-box;
  cursor: pointer;
}

.product_detail_wrapper .main_pic div {
  width: 100%;
  height: 446px;
  border: 1px solid #d9d9d9;
  background-size: contain !important;
  background-position: center center !important;
}

.product_detail_wrapper .main_thumbs {
  margin-top: 10px;
}

.product_detail_wrapper .main_thumbs .thumb {
  cursor: pointer;
  height: 300px;
  width: 50%;
  background-size: cover !important;
  background-position: top center !important;
  border: 1px solid #e8e8e8;
  box-sizing: border-box;
  float: left;
}

.payment_button_container {
  margin: 10px 0px;
}

.register_login_container {
  display: flex;
  box-sizing: border-box;
  margin: 0 auto;
  min-height: 430px;
}

.register_login_container .left,
.register_login_container .right {
  flex: 1 1;
}

.register_login_container h2 {
  margin: 20px 0px;
  text-transform: uppercase;
  color: #686868;
}

.register_login_container p {
  font-weight: 300;
  font-size: 16px;
  width: 90%;
}

.user_container {
  display: flex;
  margin-bottom: 50px;
  min-height: 700px;
}

.user_container .user_left_nav {
  border-right: 1px solid #f8f8f8;
}

.user_container .user_left_nav .links a {
  display: block;
  border-bottom: 1px solid #dddddd;
  font-size: 20px;
  font-weight: 300;
  padding: 10px 0px;
  width: 280px;
}

.user_container .user_right {
  flex-grow: 1;
  padding: 0 0 0 29px;
}

.user_container .user_nfo_panel {
  background: #f8f8f8;
  padding: 20px;
  margin-top: 20px;
}

.user_container .user_nfo_panel h1 {
  margin: 0 0 20px 0;
  line-height: 32px;
  text-transform: uppercase;
  font-size: 25px;
}

.user_container .user_nfo_panel span {
  display: block;
  font-size: 18px;
  font-weight: 300;
}

.user_product_block {
  display: flex;
  flex-wrap: wrap;
  background: white;
  padding: 10px;
  margin-bottom: 10px;
  border: 1px solid #ebebeb;
}

.user_product_block .item {
  flex-grow: 1;
}

.user_product_block .item .image {
  height: 125px;
  background-size: contain !important;
  background-repeat: no-repeat !important;
  background-position: center center !important;
  margin-right: 10px;
}
.user_product_block .item.btn {
  width: 0px;
}

.cart_remove_btn {
  cursor: pointer;
  background: #222222;
  display: inline-block;
  padding: 5px 25px;
  color: #fff;
  border-radius: 23px;
}

.admin_category_wrapper {
  padding-bottom: 50px;
  border-bottom: 4px solid #e9e9e9;
}

.admin_two_column {
  display: flex;
}

.admin_two_column .right {
  flex-grow: 1;
}
.brands_container {
  height: 200px;
  overflow-y: scroll;
  margin-right: 20px;
}

.brands_container .category_item {
  width: 200px;
  font-size: 20px;
  font-weight: 300;
  border: 1px solid #f1f1f1;
  padding: 10px;
  position: relative;
  background: #999592;
  color: #fff;
}

.dropzone {
  border: 1px solid #e5e5e5;
  margin-bottom: 40px;
}

.dropzone_box {
  display: inline-block;
  width: 25%;
  height: 150px;
  padding: 15px;
  box-sizing: border-box;
}

.dropzone_box .wrap {
  background: #eaeaea;
  width: 100%;
  height: 100%;
  text-align: center;
  box-sizing: border-box;
  font-size: 80px;
  background-size: contain !important;
  background-position: center center !important;
  border: 1px solid #eaeaea;
}

.wrap-table-shopping-cart {
  border-bottom-left-radius: 4px;
  border-bottom-right-radius: 4px;
}

.container-table-cart::before {
  content: "";
  display: block;
  position: absolute;
  width: 1px;
  height: calc(100% - 51px);
  background-color: #e6e6e6;
  top: 551px;
  left: 0;
}

.container-table-cart::after {
  content: "";
  display: block;
  position: absolute;
  width: 1px;
  height: calc(100% - 51px);
  background-color: #e6e6e6;
  top: 51px;
  right: 0;
}

.table-shopping-cart {
  border-collapse: collapse;
  width: 100%;
  min-width: 900px;
}
.container-table-cart h1 {
  text-align: center;
}

.table-shopping-cart .column-1 {
  width: 225px;
  padding-left: 50px;
}
.table-shopping-cart .column-2 {
  width: 300px;
  padding-right: 30px;
  text-align: center;
}
.table-shopping-cart .column-3 {
  width: 133px;
  padding-right: 30px;
  text-align: center;
}
.table-shopping-cart .column-4 {
  width: 300px;
  padding-right: 30px;
  text-align: center;
}
.table-shopping-cart .column-5 {
  padding-right: 30px;
  text-align: center;
}
.table-shopping-cart .table-head th {
  font-family: Montserrat-Regular;
  font-size: 13px;
  color: #555555;
  line-height: 1.5;
  text-transform: uppercase;
  padding-top: 16px;
  padding-bottom: 16px;
  border: none;
}

.table-shopping-cart .table-row {
  border: 1px solid #e6e6e6;
}

.table-shopping-cart td {
  font-family: Montserrat-Regular;
  font-size: 16px;
  color: #555555;
  line-height: 1.5;
  padding-top: 37px;
  padding-bottom: 30px;

  border: none;
}
.cart-img-product {
  width: 90px;
  position: relative;
}

.cart-img-product .image {
  width: 100%;
}

.cart-img-product .image:hover {
  cursor: pointer;
  opacity: 0.8;
}

